import { ChangeEvent, ReactNode, useId, useState } from "react";
import {
  ISelectCardContext,
  ISelectCardOnChangeOptions,
  ISelectCardState,
  SelectCardContext,
} from "./SelectCardContext";

/**
 * Props for the SelectCardProvider component
 * @extends Omit<ISelectCardContext, "radioName" | "selection">
 */
interface ISelectCardProviderProps
  extends Omit<ISelectCardContext, "radioName" | "selection"> {
  children: ReactNode;
}

/**
 * SelectCardProvider component for managing selection state across SelectCard components.
 *
 * Features:
 * - Provides context for SelectCard components to share selection state
 * - Manages selection mode (single vs multiple)
 * - Handles selection state updates and callbacks
 * - Generates unique radio button names for single selection mode
 * - Maintains a set of selected card IDs
 * - Integrates with SelectCardGroup for automatic context provision
 *
 * @example
 * <SelectCardProvider
 *   selectionMode="multiple"
 *   onChange={(e, state) => console.log('Selection changed:', state)}
 * >
 *   <SelectCard id="1" onChange={() => {}}>Option 1</SelectCard>
 *   <SelectCard id="2" onChange={() => {}}>Option 2</SelectCard>
 * </SelectCardProvider>
 */
export const SelectCardProvider = ({
  children,
  onChange,
  selectionMode,
}: ISelectCardProviderProps) => {
  const [selection, setSelection] = useState<Set<string | number>>(new Set());

  const radioName = useId();

  const handleChange = (
    e?: ChangeEvent<HTMLInputElement>,
    state?: ISelectCardState,
    options?: ISelectCardOnChangeOptions,
  ) => {
    if (!state || !state.id) {
      if (!options?.internal) {
        onChange(e, state);
      }

      return;
    }

    // Set internal selection
    if (selectionMode === "single") {
      if (state.checked) {
        setSelection(new Set([state.id]));
      } else {
        setSelection(new Set());
      }
    } else {
      if (state.checked) {
        setSelection(selection.add(state.id));
      } else {
        selection.delete(state.id);
        setSelection(selection);
      }
    }

    if (!options?.internal) {
      onChange(e, state);
    }
  };

  const context = {
    selectionMode,
    onChange: handleChange,
    radioName,
    selection,
  };

  return (
    <SelectCardContext.Provider value={context}>
      {children}
    </SelectCardContext.Provider>
  );
};
